@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  max-width: 780px;
  margin-left: auto;
  margin-right: auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.iconswitch {
   color: rgb(73, 73, 73);
   margin: 5px;
   margin-top: 20px;
   margin-right: 30px;
   margin-left: auto;
   font-size: 24px;
   transition: color 0.3s ease;
   display: block;
   width: 50px;
   cursor: pointer;
}

.iconswitch:hover {
   color: rgb(119, 119, 119);
}

.cardview {
   display: flex;
   max-width: 780px;
   min-width: 220px;
   flex-wrap: wrap;
   justify-content: center;
   margin-left: auto;
   margin-right: auto;
}

.cardview-item {
   width: 200px;
   height: 200px;
   margin: 30px;
   display: block;
   position: relative;
   box-shadow: 5px 8px 7px #ececec;
}

.cardview-item-img {
   width: 200px;
   position: absolute;
   top: 0;
   left: 0;
   z-index: 0;
}

p {
   z-index: 99;
   position: relative;
   font-family: sans-serif, 'Impact';
}

.cardview-item-name {
   font-size: 14px;
   margin-bottom: 0;
   color: rgb(46, 46, 46);
}

.cardview-item-color {
   font-size: 12px;
   margin-top: 5px;
   color: rgb(85, 85, 85);
}

.cardview-item-price {
   color: rgb(216, 64, 64);
   font-size: 12px;
   position: absolute;
   bottom: 5px;
   left: 20px;
}

.cardview-item-button {
   padding: 5px 8px;
   border: 1px solid #db5c4a;
   border-radius: 25px;
   background-color: inherit;
   font-size: 10px;
   font-weight: 600;
   text-transform: uppercase;
   color: #db5c4a;
   cursor: pointer;
   z-index: 99;
   position: absolute;
   bottom: 12px;
   right: 10px;
   white-space: nowrap;
}

.listview {
   max-width: 780px;
   min-width: 220px;
   justify-content: left;
   margin-left: auto;
   margin-right: auto;
}

.listview-item-img {
   height: 50px;
   display: inline-block;
}

.listview-item {
   width: auto;
   height: 50px;
   margin: 2px;
   display: flex;
   justify-content: space-around;
   align-items: center;
   background-color: #f5f5f5;
}

.listview-item-button {
   height: 25px;
   padding: 5px 8px;
   border: 1px solid #db5c4a;
   border-radius: 25px;
   background-color: inherit;
   font-size: 10px;
   font-weight: 600;
   text-transform: uppercase;
   color: #db5c4a;
   cursor: pointer; 
}


p {
   z-index: 99;
   position: relative;
   font-family: sans-serif, 'Impact';
}

.listview-item-name {
   font-size: 14px;
   margin-bottom: 0;
   color: rgb(46, 46, 46);
}

.listview-item-color {
   font-size: 12px;
   margin-top: 5px;
   color: rgb(85, 85, 85);

}

.listview-item-price {
   color: rgb(216, 64, 64);
   font-size: 12px;
}


